import React ,{useEffect} from 'react';
import Aos from "aos";
import "aos/dist/aos.css";

const Card = (props) => {


    useEffect(() => {
        Aos.init({duration :2000});
        },[]);

    return (
        <div>
            

           
 
 <div class="card-body cardcol mycardsh mt-4" data-aos="fade-up">
   <h5 class="card-title"><span class="text1">{props.title}</span></h5>
   <b class="card-text badge bg-secondary"><span class="text1">{props.year}</span></b>
   <p class="card-text mt-2"><span class="text1">{props.desc}</span></p>
   <ul><li class="card-text"><span class="text1">{props.percent}</span></li></ul>
 </div>





        </div>
    )
}

export default Card
